body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #000;
  line-height: 1.2;
}

html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

button {
  background: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

@font-face {
  font-family: 'VCR OSD Mono';
  src: local('VCR OSD Mono'), url('./game/fonts/VCR_OSD_MONO_1.ttf') format('truetype');
}

.game {
  position: relative;
}

.game--svg {
  display: block;
  transform: translateZ(0);
}

.game-ui {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.game-ui > * {
  pointer-events: auto;
}

.engine--svg-camera * {
  transform-box: fill-box;
}

.engine--svg-camera {
  transform-box: view-box;
}
